import React, { Component } from 'react';
import { Card, Input, Button } from 'reactstrap';
import { Table } from 'react-bootstrap';
import { Logger, getSearchResult, openPageInNewTab } from '../../Utils/Utils';
import { requestHeaderGET } from '../../Rest-API/Header';

const FILE_NAME = "TaarakMehta.js";

class TaarakMehta extends Component {

    constructor(props) {
        super(props);

        this.state = {
            apiRawData: [],
            visibleEpisodes: [],
            isLoaded: false
        };
    }

    componentDidMount() {
        fetch("/assets/json/all_apisodes_tmkoc_v2.json", requestHeaderGET())
            .then(res => res.json())
            .then(
                (result) => {
                    Logger(FILE_NAME, "componentDidMount", "FETCH_1", "result", result);
                    this.setState({
                        apiRawData: result,
                        visibleEpisodes: result,
                        isLoaded: true
                    })
                },
                (error) => {
                    Logger(FILE_NAME, "componentDidMount", "FETCH_1", "error", error);
                }
            );
    }

    openEpisode = (id) => {

    }

    searchEpisodes = (event) => {
        this.setState({ isLoaded: false });

        const searchString = event.target.value;

        this.setState({
            visibleEpisodes: getSearchResult(this.state.apiRawData, searchString),
        });
        this.setState({
            isLoaded: true
        });
    }

    render() {
        Logger(FILE_NAME, "render", "", "this.state", this.state);

        return (
            <Card>
                <h3> Hello from Taarak - Mehta ~~~~ !!!!
                    <Input bsSize="lg" placeholder=" Search here ...  🔎" type="text" id="searchString" onChange={(e) => { this.searchEpisodes(e); }} /><br />
                </h3>
                {(this.state.isLoaded) && (<Table striped bordered hover>
                    <thead>

                        <tr>
                            <th>episode</th>
                            <th>id</th>
                            <th>shortDesc</th>
                            <th>releaseDate</th>
                            <th>duration</th>
                            <th>Link</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            this.state.visibleEpisodes.map((episode, index) =>
                                <tr key={index + "__EPISODE"}>
                                    <td>{episode.episode}</td>
                                    <td>{episode.id}</td>
                                    <td>{episode.shortDesc}</td>
                                    <td>{episode.releaseDate}</td>
                                    <td>
                                        {parseInt((episode.duration / 1000) / (60))}min. {parseInt((episode.duration / 1000) % 60)}sec.
                                    </td>
                                    <td>
                                        <Button onClick={() => {
                                            openPageInNewTab("https://www.sonyliv.com/details/episodes/" + episode.id + "/");
                                        }}>
                                            <img alt="cccc" height="50px" src="https://upload.wikimedia.org/wikipedia/en/f/f9/Tmkoc.jpg" />
                                        </Button>
                                    </td>
                                </tr>
                            )
                        }
                    </tbody>
                </Table>)}

            </Card>
        )
    }

}

export default TaarakMehta;